"use strict";
Object.defineProperty(exports, "__esModule", { value: true });
class Events {
    constructor() {
        this.events = {};
        this.playerEventList = [
            // 视频播放
            "play",
            "pause",
            // "seek",           // 进度跳转
            // "volume_change",  // 音量改变
            // "rate_change",    // 播放速率改变
            // 播放模式
            "source_change",
            "part_change",
            "video_change",
            // 播放器模式
            "fullscreen",
            "fullscreen_exit",
            "pip",
            "pip_exit",
            "wide",
            "wide_exit",
            "webfull",
            "webfull_exit",
            // 弹幕事件
            "danmaku_load_start",
            "danmaku_load_end",
            "danmaku_load_fail",
            "danmaku_show",
            "danmaku_hide",
            "danmaku_send",
            // "danmaku_report",     // 举报弹幕
            "danmaku_filter",
            "danmaku_add",
            // 界面事件
            "resize",
            "contextmenu_show",
            "contextmenu_hide",
            "toast_show",
            "toast_hide",
            "darkmode",
            "darkmode_off",
            // "fixcontroller",        // 开启底栏固定(未启用)
            // "fixcontroller_off",    // 取消底栏固定(未启用)
            "setting", // 设置项更改
        ];
        this.customEventList = [];
    }
    /** 添加监听 */
    on(name, listener) {
        if (this.checkEventName(name) && typeof listener === "function") {
            if (!this.events[name]) {
                this.events[name] = [];
            }
            this.events[name].push(listener);
        }
    }
    /** 移除监听 */
    off(name, listener) {
        if (this.checkEventName(name) && typeof listener === "function") {
            if (!this.events[name]) {
                this.events[name] = [];
            }
            const index = this.events[name].indexOf(listener);
            if (typeof index === "number")
                this.events[name].splice(index, 1);
        }
    }
    /** 触发事件 */
    trigger(name, ...args) {
        if (this.events[name] && this.events[name].length) {
            for (let i = 0; i < this.events[name].length; i++) {
                this.events[name][i](...args);
            }
        }
    }
    /** 注册一个自定义事件 */
    registerCustomEvent(name) {
        if (this.checkEventName(name))
            return;
        this.customEventList.push(name);
    }
    /** 检测事件名是否有效 */
    checkEventName(name) {
        if (this.playerEventList.indexOf(name) > -1 || this.customEventList.indexOf(name) > -1) {
            return true;
        }
        else {
            return false;
        }
    }
}
exports.default = Events;
